:root{
  --color-main-bg: #fbfbfb;
  --color-black: #1a1818;
  --color-white: #fff;
  --color-yellow: #ffff00b5;
  --color-blue: #6a83fb;
  --color-violet: #ae72c2;
  --color-red: #f75d83;

  --border-radius-small: 3px;
  --border-radius-medium: 5px;

  --spacing-ultrasmall: 5px;
  --spacing-small: 10px;
  --spacing-medium: 20px;
  --spacing-large: 30px;

  --font-famaly-main: sans-serif;
  --font-size-title: 26px;
  --font-size-subtitle: 18px;
  --font-bold: 600;

  --width-max: 1260px;

  --text-shadow-yellow:
    0 0 2px var(--color-white),
    0 0 10px var(--color-blue);

  --text-shadow-blue:
    0 0 2px var(--color-white),
    0 0 40px var(--color-blue);

  --box-shadow-white:
    0 16px 24px 2px rgba(255, 255, 255, 0.02), 
    0 6px 30px 5px rgba(255, 255, 255, 0.04), 
    0 8px 10px -5px rgba(255, 255, 255, 0.1);

  --box-shadow-black:
      0 16px 24px 2px rgba(0, 0, 0, 0.5), 
      0 6px 30px 5px rgba(0, 0, 0, 0.1), 
      0 8px 10px -5px rgba(0, 0, 0, 0.2);

  --transition: .4s;

  --theme-light-title: #f5f5f5;
  --theme-dark-title: #dd3c41;
  --theme-neitral-title: #f4ff50;
  --theme-default-title: var(--theme-neitral-title);

  --theme-light-bg: linear-gradient(
    to right top,
    #779ccb, #88a6d0, #98b0d4, #a8bbd9, #b7c5dd, #b5c7de, #b2c9df, #b0cbe0, #98c6da, #7ec1d1, #66bbc2, #53b5af
  );
  --theme-dark-bg: linear-gradient(
    to right top,
    #0b0101, #100101, #140101, #180000, #1b0000, #1d0003, #1e0007, #1f000c, #1d0014, #19001a, #110220, #000625
  );
  --theme-neitral-bg: linear-gradient(
    to right top, 
  #1e2132, #242838, #2a2e3d, #303543, #373c49, #434853, #50545e, #5d6169, #74767c, #8b8b90, #a2a2a4, #b9b9b9
  );
  --theme-default-bg: var(--theme-neitral-bg);
}

*, *:after, *:before{
  box-sizing: border-box;
}

body{
  margin: 0;
  padding: 0;
  background-color: #fbfbfb;
  background-color: var(--color-main-bg);
  font-family: sans-serif;
  font-family: var(--font-famaly-main);
  background: linear-gradient(
    to right top, 
  #1e2132, #242838, #2a2e3d, #303543, #373c49, #434853, #50545e, #5d6169, #74767c, #8b8b90, #a2a2a4, #b9b9b9
  );
  background: var(--theme-default-bg);
}

.title{
  line-height: 1.7;
  font-size: 26px;
  font-size: var(--font-size-title);
  text-shadow: 1px 1px 1px #321515;
  color: #f4ff50;
  color: var(--theme-default-title);
  transition: color .4s;
  transition: color var(--transition);
}

img{
  display: block;
}

ul{
  margin: 0;
  padding: 0;
  list-style: none;
}

a.active{
  text-decoration: underline;
  font-size: larger;
}

